import { Container, InteractionEvent, Sprite } from "pixi.js";
import { getGridCellIndex, getMatchedCards, getUnmatchedCard, isSuccess } from "./algorithm";
import { playFipAllAnimation, playFlipAnimation } from "./animation";
import { CARD_SIZE, MAX_COL } from "./config";
import { FLIP_TYPE } from "./enum";
import { gridView } from "./game";
import { stopCountdown } from "./time";
import { showResetButton } from "./ui";
import { wait } from "./utils";
import { clearHighlight, highlight, match } from "./view";

function includeCard(card, cardList) { return cardList.indexOf(card) != -1 }

function removeCard(card, cardList) {
    const index = cardList.indexOf(card);
    cardList.splice(index, 1)
}

let matchedCards: Container[] = [];
let userCards: Container[] = [];
let lockedCards: Container[] = [];

export const onUserTimeout = ()=>{
    removeUserInteraction();
    showResetButton();
}

export const resetUser = async () => {
    await playFipAllAnimation(FLIP_TYPE.BACK,gridView)
    addUserInteraction();
    matchedCards = [];
    userCards = [];
    lockedCards = [];
}

export const highlightCardPairs = () => {
    const cardList = getUnmatchedCard(gridView.children, matchedCards, userCards, lockedCards);
    const matched = getMatchedCards(cardList, match);
    if (matched.length > 0) {
        const [card0, card1] = matched;
        highlight(card0);
        highlight(card1);
    }
}

export const autoFlip = () => {
    const cardList = getUnmatchedCard(gridView.children, matchedCards, userCards, lockedCards);
    const matched = getMatchedCards(cardList, match);
    if (matched.length > 0) {
        const [card0, card1] = matched;
        matchedCards.push(card0, card1);
        clearHighlight(card0);
        clearHighlight(card1);
        playFlipAnimation(FLIP_TYPE.FRONT, card0);//
        playFlipAnimation(FLIP_TYPE.FRONT, card1);//
        if (isSuccess(matchedCards, gridView.children)) {
            onSuccess();
        }
    }
}

const onSuccess = () => {
    removeUserInteraction();
    stopCountdown();
    wait(1000).then(() => {
        showResetButton();
        alert('');
    });
}

const onUserClick = async (e: InteractionEvent) => {
    const gridView = e.target as Container;
    const { global } = e.data;
    const index = getGridCellIndex([global.x, global.y], CARD_SIZE[0], CARD_SIZE[1], MAX_COL);
    const clickedCard = gridView.children[index] as Sprite;//

    if (includeCard(clickedCard, matchedCards)//
        || includeCard(clickedCard, userCards) //userCards
        || includeCard(clickedCard, lockedCards)) //
        return;

    clearHighlight(clickedCard);

    userCards.push(clickedCard);//1

    const currentMatchedCards = getMatchedCards(userCards, match);//

    matchedCards = matchedCards.concat(currentMatchedCards);//currentMatchedCards

    currentMatchedCards.forEach(matchedCard => removeCard(matchedCard, userCards));//userCards

    playFlipAnimation(FLIP_TYPE.FRONT, clickedCard);//

    if (isSuccess(matchedCards, gridView.children)) {
        onSuccess();
    } else {
        await wait(1000);//wait
        if (!includeCard(clickedCard, matchedCards)) { //
            playFlipAnimation(FLIP_TYPE.BACK, clickedCard).then(() => {
                removeCard(clickedCard, lockedCards)
            });
            removeCard(clickedCard, userCards);
            lockedCards.push(clickedCard);
        }
    }
}

export const addUserInteraction = () => {
    gridView.interactive = true;
    gridView.on('pointerdown', onUserClick);
}
export const removeUserInteraction = () => {
    gridView.interactive = false;
    gridView.off('pointerdown', onUserClick);
}